








































































import { Vue, Component } from 'vue-property-decorator';
import { CheckboxesData } from '@/types/views/components/checkboxes.interface';

@Component
export default class CheckboxesView extends Vue {
  textItems!: object[];
  checkboxesItems!: any[];

  data(): CheckboxesData {
    return {
      textItems: [
        {
          text: '复选框允许用户从一组中选择一个或多个项目。复选框可用于打开或关闭选项。使用复选框可以：',
          lists: [
            '◇ 从列表中选择一个或多个选项',
            '◇ 显示包含子选择的列表',
            '◇ 在桌面环境中打开或关闭项目'
          ],
        },
        { text: '【注意】：当列表中只能选择一项时，请勿使用复选框。请改用单选按钮。' }
      ],
      checkboxesItems: [
        {
          title: '默认',
          texts: ['复选框可用于打开或关闭选项。'],
          checkboxes: [
            {
              items: [
                { label: '苹果' },
                { label: '雪梨' },
                { label: '香蕉' }
              ],
              selected: ['苹果']
            }
          ]
        },
        {
          title: '禁用',
          texts: ['单选按钮的禁用状态'],
          checkboxes: [
            {
              items: [
                { label: '苹果', disabled: true },
                { label: '雪梨', disabled: true, indeterminate: true },
                { label: '香蕉', disabled: true }
              ],
              selected: ['苹果']
            }
          ]
        },
        {
          title: '父与子复选框',
          texts: [
            '复选框可以与其他复选框具有父子关系。',
            '◆ 选中父复选框后，所有子复选框均被选中',
            '◆ 如果未选中父复选框，则所有子复选框均未选中',
            '◆ 如果选中了部分（但不是全部）子复选框，则父复选框变为不确定的复选框'
          ],
          checkboxes: [
            {
              items: [
                { label: '苹果' },
                { label: '雪梨' },
                { label: '香蕉' }
              ],
              selected: ['苹果'],
              parent: {
                label: '全选'
              }
            }
          ]
        },
        {
          title: '大小',
          texts: ['复选框组件提供默认、紧密2种不同的尺寸，可以在不同场景下选择合适的尺寸。'],
          checkboxes: [
            {
              items: [
                { label: '苹果' },
                { label: '雪梨', indeterminate: true },
                { label: '香蕉' },
                { label: '苹果', dense: true },
                { label: '雪梨', dense: true, indeterminate: true },
                { label: '香蕉', dense: true }
              ],
              selected: ['苹果']
            }
          ]
        }
      ]
    };
  }
  get findCheckbox() {
    return this.checkboxesItems.find((element: any) => {
      return element.checkboxes[0].parent;
    }).checkboxes[0];
  }
  get selectedAll() {
    return this.findCheckbox.selected.length === this.findCheckbox.items.length
      ? true
      : false;
  }
  set selectedAll(val) {
    // if (!val && this.findCheckbox.selected.length !== this.findCheckbox.items.length) {
    if (!val) {
      this.findCheckbox.selected = [];
      // console.log(this.findCheckbox.selected);
    } else if (val) {
      // console.log(this.findCheckbox.selected);
      const selected = this.findCheckbox.items.map((element: any) => {
        return element.label;
      });
      this.findCheckbox.selected = selected;
      // console.log(this.findCheckbox.selected);
    }
  }
  get indeterminate() {
    return (checkbox: any) => {
      return 0 < checkbox.selected.length &&
        checkbox.selected.length < checkbox.items.length
        ? true
        : false;
    }
  }
}
