
































































import { Vue, Component } from 'vue-property-decorator';
import { SwitchesData } from '@/types/views/components/switches.interface';

@Component
export default class SwitchesView extends Vue {
  data(): SwitchesData {
    return {
      textItems: [
        { text: '开关可打开或关闭单个设置的状态。它们是在移动设备上调整设置的首选方法' }
      ],
      switchesItems: [
        {
          title: '默认',
          texts: [
            '当用户将开关拇指滑动到轨道的另一侧时，开关将成功切换，并且开关的状态将更改。',
            '【注意】：避免在图形本身内创建包含文本“ on”和“ off”的开关。 仅切换就足够了。'
          ],
          switches: [
            {
              items: [
                { label: '缓存图片' },
                { label: '缓存音乐' },
                { label: '缓存视频' }
              ],
              selected: ['缓存图片']
            }
          ]
        },
        {
          title: '禁用',
          texts: ['开关的禁用状态'],
          switches: [
            {
              items: [
                { label: '缓存图片', disabled: true },
                { label: '缓存音乐', disabled: true },
                { label: '缓存视频', disabled: true }
              ],
              selected: ['缓存图片']
            }
          ]
        },
        {
          title: '嵌入风格',
          texts: ['嵌入风格的开关。'],
          switches: [
            {
              items: [
                { label: '缓存图片', inset: true },
                { label: '缓存音乐', inset: true },
                { label: '缓存视频', inset: true }
              ],
              selected: ['缓存图片']
            }
          ]
        },
        {
          title: '大小',
          texts: ['开关组件提供默认、紧密2种不同的尺寸，可以在不同场景下选择合适的尺寸。'],
          switches: [
            {
              items: [
                { label: '缓存图片' },
                { label: '缓存文本', dense: true },
                { label: '缓存音乐', inset: true },
                { label: '缓存视频', inset: true, dense: true }
              ],
              selected: []
            }
          ]
        },
        {
          title: '处理状态',
          texts: [
            '因为开关显示某物的实际状态，所以有时其状态更改会有所延迟。 在这种情况下，可以使用处理状态动画。',
            '处理状态是开关拇指上的动画。 例如，当控制硬件功能的开关在确认其最终状态之前经历延迟时，可以使用它。'
          ],
          switches: [
            {
              items: [
                { label: '缓存图片', loading: 'fontColor' },
                { label: '缓存文本', dense: true, loading: 'fontColor' },
                { label: '缓存音乐', inset: true, loading: 'fontColor' },
                { label: '缓存视频', inset: true, dense: true, loading: 'fontColor' }
              ],
              selected: ['缓存图片', '缓存音乐']
            }
          ]
        }
      ]
    };
  }
}
